<template>
  <el-card>
    <!-- 1.  -->
    <my-bread level1="" level2=""></my-bread>

    <!--  -->
    <el-alert
      class="alert"
      title=""
      type="success"
      center
      show-icon
    >
    </el-alert>
    <!--  -->
    <el-steps :active="1 * active" align-center>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
      <el-step title=""></el-step>
    </el-steps>

    <!--  tabs  -->
    <el-form
      label-position="top"
      label-width="80px"
      :model="form"
      style="height: 400px; overflow: auto"
    >
      <el-tabs v-model="active" tab-position="left" @tab-click="tabChange()">
        <el-tab-pane name="1" label="">
          <el-form-item label="">
            <el-input v-model="form.goods_name"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input v-model="form.goods_price"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.goods_weight"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="form.goods_number"></el-input>
          </el-form-item>

          <el-form-item lable="">
            <!--  -->
            <el-cascader
              clearable
              expandTrigger="hover"
              v-model="selectedOptionns"
              :options="options"
              :props="defaultprop"
              @change="handleChange"
            ></el-cascader>
          </el-form-item>
        </el-tab-pane>

        <el-tab-pane name="2" label="">
          <!--  -->
          <el-form-item
            :label="item1.attr_name"
            v-for="(item1, i) in arrDyparams"
            :key="i"
          >
            <!--  -->
            <el-checkbox-group v-model="item1.attr_vals">
              <el-checkbox
                border
                :label="item2"
                v-for="(item2, i) in item1.attr_vals"
                :key="i"
              ></el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-tab-pane>

        <el-tab-pane name="3" label="">
          <!--  -->
          <el-form-item
            :label="item.attr_name"
            v-for="(item, i) in arrStaticparams"
            :key="i"
          >
            <el-input v-model="item.attr_vals"></el-input>
          </el-form-item>
        </el-tab-pane>

        <el-tab-pane name="4" label="">
          <!--  -->
          <!-- baseUrl axios  axios -->
          <el-form-item>
            <el-upload
              action="http://127.0.0.1:8888/api/private/v1/upload"
              :headers="headers"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :on-success="handleSuccess"
              list-type="picture"
            >
              <el-button size="small" type="primary"></el-button>
              <div slot="tip" class="el-upload__tip">
                jpg/png500kb
              </div>
            </el-upload>
          </el-form-item>
        </el-tab-pane>

        <el-tab-pane name="5" label="">
          <el-form-item>
            <el-button type="primary" @click="addGoods()"
              ></el-button
            >
            <!--  -->
            <quill-editor v-model="form.goods_introduce"></quill-editor>
          </el-form-item>
        </el-tab-pane>
      </el-tabs>
    </el-form>
  </el-card>
</template>
<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";

import { quillEditor } from "vue-quill-editor";

export default {
  components: {
    quillEditor,
  },
  data() {
    return {
      active: "1",
      //   
      form: {
        goods_name: "",
        goods_price: "",
        goods_number: "",
        goods_weight: "",
        goods_introduce: "",
        goods_cat: "",
        pics: [],
        attrs: [],
      },
      // 
      options: [],
      selectedOptionns: [1, 3, 6],
      defaultprop: {
        label: "cat_name",
        value: "cat_id",
        children: "children",
      },
      // 
      arrDyparams: [],
      // 
      arrStaticparams: [],
      headers: {
        Authorization: localStorage.getItem("token"),
      },
    };
  },
  created() {
    this.getGoodCate();
  },
  methods: {
    // 
    async addGoods() {
      // goods_cat
      this.form.goods_cat = this.selectedOptionns.join(",");
      // pics[] 

      // attrs[]

      //  this.form 
      const res = await this.$http.post(`goods`,this.form);
    },
    // 
    // file 
    handlePreview(file) {},
    // 
    handleRemove(file) {
      //  file.response.data.tmp_path 
      //  this.form.pics 
      // findIndex((item) => {})   
      let Index = this.form.pics.findIndex((item) => {
        return item.pic === file.response.data.tmp_path;
      });
      this.form.pics.splice(Index, 1);
    },
    // 
    handleSuccess(file) {
      //  file.data/tmp_path 
      // this.form.pics
      this.form.pics.push({
        pic: file.data.tmp_path,
      });
    },
    // tab
    async tabChange() {
      // tab  
      if (this.active === "2") {
        if (this.selectedOptionns.length !== 3) {
          //  
          this.$message.warning("");
          return;
        }
        // 
        const res = await this.$http.get(
          `categories/${this.selectedOptionns[2]}/attributes?sel=many`
        );
        // console.log(res);
        // attr_name  attr_vals
        this.arrDyparams = res.data.data;
        // this.arrDyparams  attr_vals   v-for
        this.arrDyparams.forEach((item) => {
          // 
          // if (item.attr_vals.length !== 0) {
          //   item.attr_vals = item.attr_vals.trim().split(",");
          // }
          item.attr_vals =
            item.attr_vals.length === 0 ? [] : item.attr_vals.trim().split(",");
        });
      } else if (this.active === "3") {
        if (this.selectedOptionns.length !== 3) {
          //  
          this.$message.warning("");
          return;
        }
        // 
        const res = await this.$http.get(
          `categories/${this.selectedOptionns[2]}/attributes?sel=only`
        );
        // console.log(res);
        this.arrStaticparams = res.data.data;
      }
    },
    // 
    handleChange() {},
    // 
    async getGoodCate() {
      const res = await this.$http.get(`categories?type=3`);
      // console.log(res);
      this.options = res.data.data;
    },
  },
};
</script>

<style >
.alert {
  margin-top: 20px;
}
.ql-editor {
  min-height: 200px;
}
</style>


