<template>
  <el-card>
    <!-- 1.  -->
    <!-- <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb> -->
    <!--  -->
    <my-bread level1="" level2=""></my-bread>
    <!-- 2.  -->
    <el-table class="table" :data="rightlist" border style="width: 100%">
      <el-table-column type="index" label="#" width="100"> </el-table-column>
      <el-table-column prop="authName" label="" width="180">
      </el-table-column>
      <el-table-column prop="path" label=""> </el-table-column>

      <el-table-column prop="level" label="">
        <template slot-scope="scope">
            <!-- scope.row.level 0 1 2 -->
           <span v-if="scope.row.level === '0'"></span>
           <span v-if="scope.row.level === '1'"></span>
           <span v-if="scope.row.level === '2'"></span>
        </template>
      </el-table-column>

    </el-table>
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      rightlist: [],
    };
  },
  created() {
    this.getRightlist();
  },
  methods: {
    async getRightlist() {
      const res = await this.$http.get(`rights/list`);
      console.log(res);
      this.rightlist = res.data.data;
    },
  },
};
</script>

<style>
.table{
  margin-top: 15px;
  overflow: auto;
  height: 560px;
  
}
</style>


