<template>
  <el-card>
    <!--  -->
    <my-bread level1="" level2=""></my-bread>
    <!--  -->
    <el-row>
      <el-col :span="24" class="searchArea">
        <el-button type="success" @click="addGoodsCate()"></el-button>
      </el-col>
    </el-row>

    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleAdd">
      <el-form :model="form">
        <el-form-item label="" label-width="120px">
          <el-input v-model="form.cat_name" autocomplete="off"></el-input>
        </el-form-item>
        {{selectedOptionns}}
        <!--  () -->
        <el-form-item label="" label-width="120px">
          <el-cascader
            props.expandTrigger='hover'
            props.checkStrictly
            clearable
            :options="caslist"
            v-model="selectedOptionns"
            :props="defaultProp"
          >
          </el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false"></el-button>
        <el-button type="primary" @click="addCate()"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-table :data="list" style="width: 100%" height="470">
      <!-- treeKey > nodeKey  >  id
          parentKey > id
          levelKey  >  
          childKey  > 
     -->
      <el-tree-grid
        prop="cat_name"
        label=""
        treeKey="cat_id"
        parentKey="cat_pid"
        levelKey="cat_level"
        childKey="children"
      >
      </el-tree-grid>
      <el-table-column label="">
        <template slot-scope="scope">
          <span v-if="scope.row.cat_level === 0"></span>
          <span v-else-if="scope.row.cat_level === 1"></span>
          <span v-else-if="scope.row.cat_level === 2"></span>
        </template>
      </el-table-column>

      <el-table-column label="">
        <template slot-scope="scope">
          <span v-if="scope.row.cat_deleted === false"></span>
          <span v-else-if="scope.row.cat_deleted === true"></span>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <!--  -->
          <el-button
            size="mini"
            plain
            type="primary"
            icon="el-icon-edit"
            circle
            @click="showEditdia(scope.row)"
          ></el-button>
          <!--  -->
          <el-button
            size="mini"
            plain
            type="danger"
            icon="el-icon-delete"
            circle
            @click="showDelefirm(scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 4.  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[2, 4, 6, 8]"
      :page-size="8"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>
  </el-card>
</template>
<script>
// elment-tree-grid
var ElTreeGrid = require("element-tree-grid");
export default {
  components: {
    // <el-tree-grid>
    ElTreeGrid,
  },
  data() {
    return {
      list: [],
      dialogFormVisibleAdd: false,
      form: {
        cat_pid: -1,
        cat_name: "",
        cat_level: -1,
      },
      selectedOptionns:[],
      caslist: [],
      defaultProp: {
        value: "cat_id",
        label: "cat_name",
        children: "children",
      },
      //   
      total: -1,
      pagenum: 1,
      pagesize: 8,
    };
  },
  created() {
    this.getGoodsCate();
  },
  methods: {
    //    
    async addCate() {
      // cat_pid:-1,
      // cat_name:'',
      // cat_level:-1
      //  selectedOptionns.length === 0 cat_pid cat_level =0
      //  selectedOptionns.length === 1 cat_pid=selectedOptionns[0] cat_level =1
      //  selectedOptionns.length === 2 cat_pid=selectedOptionns[1] cat_level =2
      if (this.selectedOptionns.length === 0) {
        this.form.cat_pid = 0;
        this.form.cat_level = 0;
      } else if (this.selectedOptionns.length === 1) {
        this.form.cat_pid = this.selectedOptionns[0];
        this.form.cat_level = 1;
      } else if (this.selectedOptionns.length === 2) {
        this.form.cat_pid = this.selectedOptionns[1];
        this.form.cat_level = 2;
      }
      const res = await this.$http.post(`categories`,this.form);
      //  
      this.getGoodsCate();

      // 
      this.dialogFormVisibleAdd = false;
      // form
      this.form = {};
    },
    //  
    async addGoodsCate() {
      // 
      const res = await this.$http.get(`categories?type=2`);
      this.caslist = res.data.data;
      this.dialogFormVisibleAdd = true;
    },
    async getGoodsCate() {
      const res = await this.$http.get(
        `categories?type=3&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
      );
      this.list = res.data.data.result;
      this.total = res.data.data.total;
    },
    // 
    handleSizeChange(val) {
      // console.log(` ${val} `);
      this.pagesize = val;
      this.pagenum = 1;
      this.getGoodsCate();
    },
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.pagenum = val;
      this.getGoodsCate();
    },
    // 
    // 
    showEditdia() {},
    // 
    showDelefirm() {},
  },
};
</script>

<style>
.searchArea {
  margin-top: 20px;
}
.table {
  margin-top: 20px;
}
</style>


