<template>
  <el-container class="container">
      <!--  -->
    <el-header class="header">
      <el-row>
        <el-col :span="4">
          <div class="grid-content bg-purple">
            <img class="logo" src="../../assets/logo.png" alt="" />
          </div>
        </el-col>
        <el-col :span="18" class="middle">
          <h3></h3>
        </el-col>
        <el-col :span="2">
          <div class="grid-content bg-purple">
            <a class="loginout" href="#" @click.prevent="handleSignout()"></a>
          </div>
        </el-col>
      </el-row>
    </el-header>
    <el-container>
      <el-aside class="aside" width="200px">
        <!--  el-menu  -->
        <!-- :router='true'  -->
        <el-menu
        :router='true'
        :unique-opened='true'
        >
          <el-submenu :index="''+item1.order" v-for="(item1,index) in menus" :key="index">
            <template slot="title">
              <i class="el-icon-location"></i>
              <span>{{item1.authName}}</span>
            </template>
            <el-menu-item 
            :index="item2.path" 
            v-for="(item2,index) in item1.children" 
            :key="index">
              <i class="el-icon-menu"></i>
              <span>{{item2.authName}}</span>
            </el-menu-item>
          </el-submenu>



        </el-menu>
      </el-aside>
      <el-main class="main">
        <!--  -->
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  data() {
   return {
     menus: []
   }
  },
  //  newVue
 beforeCreate() {
   // token
  //  const token = localStorage.getItem('token')
  
  // if(!token){
   // token  - 
  //  this.$router.push({name:'login'})
  // }
  // if token - 
 },
 created() {
   this.getMenus()
 },
  methods: {
    // 
    async getMenus(){
     const res = await this.$http.get(`menus`)
    //  console.log(res)
     this.menus = res.data.data
    },
    handleSignout(){
      //1 token
      localStorage.clear()
      //2 
      this.$message.success('')
      //3 login
      this.$router.push({name:'login'})
    }
  },
}
</script>

<style>
.container {
  height: 100%;
}
.header {
  background-color: #b3c0d1;
}
.aside {
  background-color: #d3dcd6;
}
.main {
  background-color: #e9eef3;
}

/*  */
.logo {
  height: 60px;
}
.middle {
  text-align: center;
}
.loginout {
  line-height: 60px;
  text-decoration: none;
}
</style>



