<template>
  <el-card class="box-card">
    <!-- 1.  -->
    <my-bread level1="" level2=""></my-bread>
    <!-- 2.  -->
    <el-row class="searchRow">
      <el-col>
        <el-input
          @clear="loadUserList()"
          placeholder=""
          v-model="query"
          clearable
          class="inputSearch"
        >
          <!--  -->
          <el-button @click="searchUser()" slot="append" icon="el-icon-search">
          </el-button>
        </el-input>
        <!--  -->
        <el-button type="success" plain @click="$router.push({name:'goodsadd'})"></el-button>
      </el-col>
    </el-row>

    <!-- 3.  -->
    <el-table :data="goodslist" style="width: 100%" stripe border>
      <el-table-column type="index" label="#" width="60"> </el-table-column>
      <el-table-column prop="goods_name" label="" width="600">
      </el-table-column>
      <el-table-column prop="goods_price" label="()" width="80"> </el-table-column>
      <el-table-column prop="goods_weight" label="" width="80"> </el-table-column>

      <!-- <el-table-column prop="create_time | fmtdate" label=""> </el-table-column> -->
      <!-- <el-table-column prop="create_time" label="">{{create_time | fmtdate}}</el-table-column> -->
      <el-table-column label="">
        <!-- 
               template -->
        <!-- 
                template slot-scope
                create_timeuserlist
             -->
        <!--  slot-scopeuserlistel-tableuserlist
                   userlist.row -> 

                   scope -> userlist
              -->
        <template slot-scope="scope">
          {{ scope.row.create_time | fmtdate }}
        </template>
      </el-table-column>

      
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <!--  -->
          <el-button
            size="mini"
            plain
            type="primary"
            icon="el-icon-edit"
            circle
            @click="showEditUserDia(scope.row)"
          ></el-button>
          <!--  -->
          <el-button
            size="mini"
            plain
            type="danger"
            icon="el-icon-delete"
            circle
            @click="showDeleUserMsgBox(scope.row.id)"
          ></el-button>
          <!--  -->
          <el-button
            @click="showSetUserRoleDia(scope.row)"
            size="mini"
            plain
            type="success"
            icon="el-icon-check"
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 4.  -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="pagenum"
      :page-sizes="[2, 4, 6, 8]"
      :page-size="6"
      layout="total, sizes, prev, pager, next, jumper"
      :total="total"
    >
    </el-pagination>

    <!--  -->
    <!-- 1. -->
    <el-dialog title="" :visible.sync="dialogFormVisibleAdd">
      <el-form :model="form">
        <el-form-item label="" label-width="100px">
          <el-input v-model="form.username" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.password" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleAdd = false"> </el-button>
        <el-button type="primary" @click="addUser()"> </el-button>
      </div>
    </el-dialog>
    <!-- 2. -->
    <el-dialog title="" :visible.sync="dialogFormVisibleEdit">
      <el-form :model="form">
        <el-form-item label="" label-width="100px">
          <el-input
            disabled
            v-model="form.username"
            autocomplete="off"
          ></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.email" autocomplete="off"></el-input>
        </el-form-item>

        <el-form-item label="" label-width="100px">
          <el-input v-model="form.mobile" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleEdit = false"> </el-button>
        <el-button type="primary" @click="editUser()"> </el-button>
      </div>
    </el-dialog>
    <!-- 3. -->
    <el-dialog title="" :visible.sync="dialogFormVisibleRol">
      <el-form :model="form">
        <el-form-item label="" label-width="100px">
          {{currUsername}}
        </el-form-item>
        <el-form-item label="" label-width="100px">
          
          <!-- select  optionvalue optionlabel -->
          <el-select v-model="currRoleId">
            <el-option label="" :value="-1"></el-option>
            <el-option :label="item.roleName" :value="item.id"
            v-for="(item,i) in roles"
            :key="i"
            ></el-option>
          </el-select>

        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleRol = false"> </el-button>
        <el-button type="primary" @click="setRole()"> </el-button>
      </div>
    </el-dialog>
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      query: "",

      //   
      // username: "admin"
      // email: "adsfad@qq.com"
      // mobile: "12345678"
      // create_time: 1486720211
      // mg_state: true
      // id: 500
      // role_name: ""

      goodslist: [],
      //   
      total: -1,
      pagenum: 1,
      pagesize: 6,
      // 
      dialogFormVisibleAdd: false,
      dialogFormVisibleEdit: false,
      dialogFormVisibleRol: false,
      // 
      // username |  |  |
      // | password |  |  |
      // | email    |      |  |
      // | mobile   |    |  |
      form: {
        username: "",
        password: "",
        email: "",
        mobile: "",
      },
      //
      currRoleId: 0,
      currUserId:-1,
      currUsername: '',
      // 
      roles: [],
    };
  },
  created() {
    this.getUserList();
  },
  methods: {
    //  
    async setRole () {
       const res = await this.$http.put(`users/${this.currUserId}/role`,{
         rid:this.currRoleId
       })
      //  
       this.dialogFormVisibleRol = false
    },
    //  
    async showSetUserRoleDia(user) {
      this.currUsername = user.username
      // currUserId
      this.currUserId = user.id
      // 
         const res1 = await this.$http.get(`roles`)
         this.roles = res1.data.data
      // id - rid
      const res = await this.$http.get(`users/${user.id}`)
      // id rid   res.data.data.rid
       this.currRoleId = res.data.data.rid
      this.dialogFormVisibleRol = true
    },
    // 
    async changeMgState(user) {
      const res = await this.$http.put(
        `users/${user.id}/state/${user.mg_type}`
      );
    },
    //  
    async editUser() {
      const res = await this.$http.put(`users/${this.form.id}`, this.form);
      // console.log(res)
      // 1. 
      this.dialogFormVisibleEdit = false;
      // 2. 
      this.getUserList();
    },
    //  
    showEditUserDia(user) {
      this.form = user;
      // 
      this.dialogFormVisibleEdit = true;
    },
    //  
    showDeleUserMsgBox(userId) {
      this.$confirm("", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(async () => {
          //  id >>> id
          // 1. data  userId     x
          // 2. userId showDeleUserMsgBox 
          const res = await this.$http.delete(`users/${userId}`);
          console.log(res);
          if (res.data.meta.status === 200) {
            // 
            this.pagenum = 1;
            // 
            this.getUserList();
            // 
            this.$message({
              type: "success",
              message: res.data.meta.msg,
            });
          }
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    //  
    async addUser() {
      // 2. 
      this.dialogFormVisibleAdd = false;

      const res = await this.$http.post(`users`, this.form);
      // console.log(res);
      const {
        meta: { status, msg },
        data,
      } = res.data;
      if (status === 200) {
        // 1. 
        this.$message.success(msg);
        // 3. 
        this.getUserList();
        // 4. 
        // this.form = {} 
        // for in
        for (const key in this.form) {
          if (this.form.hasOwnProperty(key)) {
            this.form[key] = "";
          }
        }

        // 
      } else {
        this.$message.warning(msg);
      }
    },
    //
    async getUserList() {
      //query   
      //pagenum    
      //pagesize    

      // API  `Authorization`  `token` 
      //  axios

      
      const res = await this.$http.get(
        `goods?query=${this.query}&pagenum=${this.pagenum}&pagesize=${this.pagesize}`
       
      );
//  console.log(res)
      const {
        meta: { status, msg },
        data: { goods, total },
      } = res.data;

      if (status === 200) {
        //   1.
        this.goodslist = goods;
        // 2. total
        this.total = total;
        // 3. 
        // this.$message.success(msg);
      } else {
        //   
        // this.$message.warning(msg);
      }
    },
    // 
    handleSizeChange(val) {
      
      this.pagesize = val;
      this.pagenum = 1;
      this.getUserList();
    },
    handleCurrentChange(val) {
      
      this.pagenum = val;
      this.getUserList();
    },

    // 
    searchUser() {
      this.getUserList();
    },
    //   
    loadUserList() {
      this.getUserList();
    },
    //  
    showAddUserDia() {
      this.form = {};
      this.dialogFormVisibleAdd = true;
    },
  },
};
</script>
<style>
.box-card {
  height: 100%;
}
.inputSearch {
  width: 400px;
}
.searchRow {
  margin-top: 20px;
}
</style>


