<template>
  <el-card>
    <!--  -->
    <my-bread level1="" level2=""></my-bread>
    <el-row>
      <el-col>
        <el-button type="info" class="addrolebut"></el-button>
      </el-col>
    </el-row>

    <!--  -->
    <el-table :data="rolelist" style="width: 100%">
      <el-table-column type="expand" width="150">
        <template slot-scope="scope">
          <!--  -->
          <el-row v-for="(item1, i) in scope.row.children" :key="i">
            <el-col :span="4">
              <!-- id  id -->
              <el-tag
                @close="deleRight(scope.row, item1.id)"
                closable
                type="success"
                >{{ item1.authName }}</el-tag
              >
              <i class="el-icon-arrow-right"></i>
            </el-col>
            <el-col :span="20">
              <!--  -->
              <el-row v-for="(item2, i) in item1.children" :key="i">
                <el-col :span="4">
                  <el-tag
                    @close="deleRight(scope.row, item2.id)"
                    closable
                    type="info"
                    >{{ item2.authName }}</el-tag
                  >
                  <i class="el-icon-arrow-right"></i>
                </el-col>
                <!--  -->
                <el-col :span="20">
                  <el-tag
                    @close="deleRight(scope.row, item3.id)"
                    closable
                    type="warning"
                    v-for="(item3, i) in item2.children"
                    :key="i"
                  >
                    {{ item3.authName }}
                  </el-tag>
                </el-col>
              </el-row>
            </el-col>
          </el-row>

          <!--  -->
          <span v-if="scope.row.children.length === 0"></span>
        </template>
      </el-table-column>
      <el-table-column type="index" label="#" width="150"> </el-table-column>
      <el-table-column prop="roleName" label="" width="200">
      </el-table-column>
      <el-table-column prop="roleDesc" label="" width="200">
      </el-table-column>
      <el-table-column prop="address" label="">
        <template slot-scope="scope">
          <!--  -->
          <el-button
            size="mini"
            plain
            type="primary"
            icon="el-icon-edit"
            circle
            @click="showEditUserDia(scope.row)"
          ></el-button>
          <!--  -->
          <el-button
            size="mini"
            plain
            type="danger"
            icon="el-icon-delete"
            circle
            @click="showDeleUserMsgBox(scope.row.id)"
          ></el-button>
          <!--  -->
          <el-button
            @click="showSetRightDia(scope.row)"
            size="mini"
            plain
            type="success"
            icon="el-icon-check"
            circle
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisibleRight">
      <!-- 
          data >  []
          show-checkbox   
          node-key   datakeyid
          default-expanded-keys  id
          default-checked-keys id
          props   
                  :default-expanded-keys="[2, 3]"
                  :default-checked-keys="[5]"
       -->
      <el-tree
      ref="tree"
        :data="treelist"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="arrcheck"
        :props="defaultProps"
      >
      </el-tree>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisibleRight = false"> </el-button>
        <el-button type="primary" @click="setRoleRight()"
          > </el-button
        >
      </div>
    </el-dialog>
  </el-card>
</template>
<script>
export default {
  data() {
    return {
      rolelist: [],
      dialogFormVisibleRight: false,
      // 

      treelist: [],
      defaultProps: {
        label: "authName",
        children: "children",
      },
      arrexpand: [],
      arrcheck: [],
      currRoleId: -1,
    };
  },
  created() {
    this.getRolelist();
  },
  methods: {
    //  
    async setRoleRight() {
      //  roleId ID
      // rids  labelid []
      //  idarr1  getCheckedKeys()   arr2 getHalfCheckedKeys()     arr=arr1+arr2
      let arr1 = this.$refs.tree.getCheckedKeys()
      let arr2 = this.$refs.tree.getHalfCheckedKeys()
      // es6  ...
      let arr = [...arr1,...arr2]

     const res = await this.$http.post(`roles/${this.currRoleId}/rights`,{rids:arr.join(',')})
     console.log(res)
    //  
    this.getRolelist()
    //  
     this.dialogFormVisibleRight = false
    },
    //  
    async showSetRightDia(role) {
      this.currRoleId = role.id
      // 
      const res = await this.$http.get(`rights/tree`);
      // console.log(res);
      this.treelist = res.data.data;
      //
      // var arrtemp1 = [];
      // this.treelist.forEach((item1) => {
      //   arrtemp1.push(item1.id);
      //   item1.children.forEach((item2) => {
      //     arrtemp1.push(item2.id);
      //     item2.children.forEach((item3) => {
      //       arrtemp1.push(item3.id);
      //     });
      //   });
      // });

      //  console.log(arrtemp1)
      //  this.arrexpand = arrtemp1

      // role id
      let arrtemp2 = [];
      role.children.forEach((item1) => {
        // arrtemp2.push(item1.id);
        item1.children.forEach((item2) => {
          // arrtemp2.push(item2.id);
          item2.children.forEach((item3) => {
            arrtemp2.push(item3.id);
          });
        });
      });
      this.arrcheck = arrtemp2;

      this.dialogFormVisibleRight = true;
    },
    // 
    async deleRight(role, rightId) {
      //  roles/:roleId/rights/:rightId
      // roleId id
      // rightId id
      const res = await this.$http.delete(`roles/${role.id}/rights/${rightId}`);
      // console.log(res);
      //    200  
      // 
      role.children = res.data.data;
      // this.getRolelist()
    },
    async getRolelist() {
      const res = await this.$http.get(`roles`);
      // console.log(res);
      this.rolelist = res.data.data;
    },
  },
};
</script>

<style>
.addrolebut {
  margin-top: 20px;
}
</style>


